import 'swiper/swiper.scss';
import { Swiper } from 'swiper/swiper.esm.js';
document.addEventListener('DOMContentLoaded', function (event) {
  var prodThumbSwiper;
  var prodMainSwiper;
  var skip = false;
  var sliderSpeed = 500;
  prodThumbSwiper = new Swiper('.prodThumbSlider', {
    loop: false,
    slidesPerView: 3,
    spaceBetween: 10,
    speed: sliderSpeed,
    setWrapperSize: true,
    watchSlidesProgress: true,
    slideToClickedSlide: true,
    on: {
      slideChange: function slideChange(swp) {
        if (!skip && prodMainSwiper) {
          skip = true;
          prodMainSwiper.slideTo(swp.activeIndex);
        }
      },
      slideChangeTransitionEnd: function slideChangeTransitionEnd() {
        skip = false;
      },
      init: function init(swp) {
        var _loop = function _loop(i) {
          var sld = swp.slides[i];

          sld.onclick = function () {
            prodMainSwiper.slideTo(+sld.getAttribute('data-slide-index'));
          };
        };

        for (var i = 0; i < swp.slides.length; i++) {
          _loop(i);
        }
      }
    }
  });
  prodMainSwiper = new Swiper('.prodMainSlider', {
    loop: false,
    setWrapperSize: true,
    speed: sliderSpeed,
    thumbs: {
      swiper: prodThumbSwiper
    },
    on: {
      slideChange: function slideChange(swp) {
        if (!skip && prodThumbSwiper) {
          skip = true;
          prodThumbSwiper.slideTo(swp.activeIndex);
        }
      },
      slideChangeTransitionEnd: function slideChangeTransitionEnd() {
        skip = false;
      }
    }
  });
});